import React from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import image from "../images/HÖNING.png";
import { createMuiTheme, responsiveFontSizes, ThemeProvider } from "@material-ui/core/styles";
import Fade from "react-reveal/Fade";
import PropTypes from "prop-types";
import { setTranslations, setDefaultLanguage, setLanguageCookie, setLanguage, translate } from "react-switch-lang";
import en from "../language/en.json";
import de from "../language/de.json";

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

theme.typography.h4 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "1.3rem",
  },
};

theme.typography.h6 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "0.8rem",
  },
};

const styles = (theme) => ({
  root: {
    [theme.breakpoints.down("sm")]: {
      marginLeft: 0,
      marginTop: 56,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 64,
    },
  },
  upperContent: {
    color: "white",
    [theme.breakpoints.up("sm")]: {
      display: "flex",
      justifyContent: "left",
      marginLeft: "3%",
      marginRight: "3%",
      // height: "93.2vh",
    },
    background: "hsl(204 100% 5%)",
    boxShadow: "10px 5px 5px black",
    fontFamily: "system-ui",
  },
  aboutme: {
    // backgroundColor: "red",
    [theme.breakpoints.up("sm")]: {
      width: 430,
      marginLeft: "15%",
      paddingTop: "5%",
      paddingBottom: "5%",
      marginRight: "5%",
    },
    [theme.breakpoints.down("sm")]: {
      paddingTop: "5%",
      paddingLeft: "10%",
      paddingRight: "15%",
      paddingBottom: "5%",
    },
  },
  lowerContent: {
    color: "black",
    // backgroundColor: purple[500],
    [theme.breakpoints.up("sm")]: {
      // height: "47.4vh",
      display: "flex",
      justifyContent: "space-evenly",
    },
  },
  image: {
    [theme.breakpoints.up("sm")]: {
      width: "100%",
      height: "auto",
    },
    [theme.breakpoints.down("sm")]: {
      width: "60%",
      height: "auto",
      display: "block",
      marginLeft: "auto",
      marginRight: "auto",
    },
  },
  picture: {
    [theme.breakpoints.up("sm")]: {
      width: "40vh",
      paddingTop: "3%",
      paddingLeft: "3%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
      paddingTop: "5%",
      paddingLeft: "10%",
      paddingRight: "10%",
      alignItems: "center",
    },
    // backgroundColor: "red",
  },
  beschreibung: {
    color: "linear-gradient(to right, hsl(98 100% 62%), hsl(204 100% 59%))",
    [theme.breakpoints.up("sm")]: {
      paddingTop: "5%",
      width: "30%",
    },
    [theme.breakpoints.down("sm")]: {
      width: 400,
      paddingTop: "5%",
      paddingLeft: "10%",
      paddingRight: "10%",
      paddingBottom: "5%",
    },
    // backgroundColor: "red",
  },
  special: {
    [theme.breakpoints.up("sm")]: {
      width: 430,
      marginLeft: "5%",
      paddingTop: "2%",
    },
    [theme.breakpoints.down("sm")]: {
      width: 400,
      paddingTop: "5%",
      paddingLeft: "10%",
      paddingRight: "10%",
      paddingBottom: "5%",
    },
  },
  goodToKnow: {
    [theme.breakpoints.up("sm")]: {
      marginLeft: "5%",
      paddingTop: "2%",
    },
    [theme.breakpoints.down("sm")]: {
      width: 400,
      paddingTop: "1%",
      paddingLeft: "10%",
      paddingRight: "10%",
      paddingBottom: "5%",
    },
  },
});

class AboutMe extends React.Component {
  handleSetLanguage = (key) => () => {
    setLanguage(key);
  };

  render() {
    const { classes } = this.props;
    const { t } = this.props;
    return (
      <div className={classes.root}>
        <div className={classes.upperContent}>
          {/* <Canvas> */}
          <div className={classes.picture}>
            <img src={image} className={classes.image} alt="Picture_Of_Henning" />
          </div>
          <div className={classes.beschreibung}>
            <ThemeProvider theme={theme}>
              <Typography variant="h2" component="h1" align="center" gutterBottom>
                Henning Weise
              </Typography>
              <Typography variant="h4" component="h1" align="center" gutterBottom>
                {t("AboutMe.studiengang1")} <br />
                {t("AboutMe.studiengang2")} <br />
                {t("AboutMe.studiengang3")}
              </Typography>
            </ThemeProvider>
          </div>
          <div className={classes.aboutme}>
            <Typography variant="h5" component="h1" gutterBottom>
              {t("AboutMe.ueberMich")}
            </Typography>
            <Typography variant="body1" component="h1" gutterBottom align="justify">
              {t("AboutMe.ueberMichText")}
            </Typography>
          </div>
          {/* </Canvas> */}
        </div>

        <div className={classes.lowerContent}>
          <div className={classes.special}>
            <Typography variant="h4" component="h1" gutterBottom>
              {t("AboutMe.specialHeader")}
            </Typography>
            <Fade bottom>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.specialText1")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.specialText2")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.specialText3")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.specialText4")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.specialText5")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.specialText6")}
              </Typography>
            </Fade>
          </div>
          <div className={classes.goodToKnow}>
            <Typography variant="h4" component="h1" gutterBottom>
              {t("AboutMe.factsHeader")}
            </Typography>
            <Fade bottom>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.factsText1")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.factsText2")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.factsText3")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.factsText4")}
              </Typography>
              <Typography variant="h6" component="h1" gutterBottom>
                {t("AboutMe.factsText5")}
              </Typography>
            </Fade>
          </div>
        </div>
      </div>
    );
  }
}

AboutMe.propTypes = {
  t: PropTypes.func.isRequired,
};

export default withStyles(styles)(translate(AboutMe));
