import React from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Timeline from "@material-ui/lab/Timeline";
import TimelineItem from "@material-ui/lab/TimelineItem";
import TimelineSeparator from "@material-ui/lab/TimelineSeparator";
import TimelineConnector from "@material-ui/lab/TimelineConnector";
import TimelineContent from "@material-ui/lab/TimelineContent";
import TimelineOppositeContent from "@material-ui/lab/TimelineOppositeContent";
import TimelineDot from "@material-ui/lab/TimelineDot";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Zoom from "react-reveal/Zoom";
import { Translate } from "@material-ui/icons";
import { setTranslations, setDefaultLanguage, setLanguageCookie, setLanguage, translate } from "react-switch-lang";
import LocationOnIcon from "@material-ui/icons/LocationOn";

const styles = (theme) => ({
  root: {
    // backgroundColor: "green",
    [theme.breakpoints.down("sm")]: {
      marginLeft: 0,
      marginTop: 56,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 90,
    },
    display: "flex",
    justifyContent: "center",
    // backgroundColor: "green",
  },
  paper: {
    [theme.breakpoints.up("sm")]: {
      padding: "6px 16px",
      width: "auto",
    },
    [theme.breakpoints.down("sm")]: {
      padding: "6px 16px",
      // width: "100%",
    },
    backgroundColor: "gray",
  },
  paperv2: {
    marginLeft: "10%",
    marginTop: "2%",
    padding: "1%",
  },
  timeline: {
    // backgroundColor: "lightblue",
  },
  item: {
    // backgroundColor: "red",
    [theme.breakpoints.down("sm")]: {
      marginLeft: -10,
      marginRight: -10,
    },
  },
  content: {
    [theme.breakpoints.up("sm")]: {
      width: "80%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
});

class Berufserfahrung extends React.Component {
  render() {
    const { classes } = this.props;
    const { t } = this.props;

    return (
      <div className={classes.root}>
        <div className={classes.content}>
          <Timeline className={classes.timeline} align="alternate">
            <TimelineItem className={classes.item}>
              <TimelineOppositeContent>
                <Typography variant="body2" color="textSecondary">
                  2009
                </Typography>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot />
                <TimelineConnector />
              </TimelineSeparator>

              <TimelineContent className={classes.timelineContent}>
                <Zoom right>
                  <Paper elevation={3} className={classes.paper}>
                    <Typography variant="h6" component="h1">
                      {t("Experience.AbiturHeader")}
                    </Typography>
                    <Typography>
                      <LocationOnIcon />
                      {t("Experience.AbiturText")}
                    </Typography>
                  </Paper>
                </Zoom>
              </TimelineContent>
            </TimelineItem>

            <TimelineItem>
              <TimelineOppositeContent>
                <Typography variant="body2" color="textSecondary">
                  {t("Experience.Dates.first")}
                </Typography>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot />
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <Zoom left>
                  <Paper elevation={3} className={classes.paper}>
                    <Typography variant="h6" component="h1">
                      Fitness and Sports Entertainer
                    </Typography>
                    <Typography>
                      <LocationOnIcon />
                      {t("Experience.TuiText")}
                    </Typography>
                  </Paper>
                </Zoom>
              </TimelineContent>
            </TimelineItem>

            <TimelineItem>
              <TimelineOppositeContent>
                <Typography variant="body2" color="textSecondary">
                  November 2017
                </Typography>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot />
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <Zoom right>
                  <Paper elevation={3} className={classes.paper}>
                    <Typography variant="h6" component="h1">
                      {t("Experience.WorkingHeader")}
                    </Typography>
                    <Typography>{t("Experience.WorkingText")}</Typography>
                  </Paper>
                </Zoom>
              </TimelineContent>
            </TimelineItem>

            <TimelineItem>
              <TimelineOppositeContent>
                <Typography variant="body2" color="textSecondary">
                  {t("Experience.Dates.second")}
                </Typography>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot />
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <Zoom left>
                  <Paper elevation={3} className={classes.paper}>
                    <Typography variant="h6" component="h1">
                      {t("Experience.StuttgartHeader")}
                    </Typography>
                    <Typography>
                      <LocationOnIcon />
                      {t("Experience.StuttgartText")}
                    </Typography>
                  </Paper>
                </Zoom>
              </TimelineContent>
            </TimelineItem>

            <TimelineItem>
              <TimelineOppositeContent>
                <Typography variant="body2" color="textSecondary">
                  {t("Experience.Dates.third")}
                </Typography>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot />
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <Zoom right>
                  <Paper elevation={3} className={classes.paper}>
                    <Typography variant="h6" component="h1">
                      {t("Experience.EsslingenHeader")}
                    </Typography>
                    <Typography>
                      <LocationOnIcon />
                      {t("Experience.EsslingenText")}
                    </Typography>
                  </Paper>
                </Zoom>
                <Zoom right>
                  <Paper elevation={3} className={classes.paperv2}>
                    <Typography variant="h6" component="h1">
                      {t("Experience.TutorHeader")}
                    </Typography>
                    <Typography>{t("Experience.TutorText")}</Typography>
                  </Paper>
                </Zoom>
                <Zoom right>
                  <Paper elevation={3} className={classes.paperv2}>
                    <Typography variant="h6" component="h1">
                      {t("Experience.HiwiHeader")}
                    </Typography>
                    <Typography>{t("Experience.HiwiText")}</Typography>
                  </Paper>
                </Zoom>
                <Zoom right>
                  <Paper elevation={3} className={classes.paperv2}>
                    <Typography variant="h6" component="h1">
                      {t("Experience.PraxisHeader")}
                    </Typography>
                    <Typography>{t("Experience.PraxisText")}</Typography>
                  </Paper>
                </Zoom>
              </TimelineContent>
            </TimelineItem>

            <TimelineItem>
              <TimelineOppositeContent>
                <Typography variant="body2" color="textSecondary">
                  {t("Experience.Dates.today")}
                </Typography>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot />
              </TimelineSeparator>
              <TimelineContent></TimelineContent>
            </TimelineItem>
          </Timeline>
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(translate(Berufserfahrung));
