import "./App.css";
import ResponsiveDrawer from "./Components/ResponsiveDrawer.js";
import { Route, Switch } from "react-router-dom";
import AboutMe from "./Components/AboutMe.js";
import Skills from "./Components/Skills.js";
import Berufserfahrung from "./Components/Berufserfahrung.js";
import Kontakt from "./Components/Kontakt.js";
import Projects from "./Components/Projects";

export default function App() {
  return (
    <main>
      <ResponsiveDrawer />
      <Switch>
        <Route exact path="/homepage" render={(props) => <AboutMe {...props} />} />
        <Route exact path="/skills" render={(props) => <Skills {...props} />} />
        <Route exact path="/myprojects" render={(props) => <Projects {...props} />} />
        <Route exact path="/journey" render={(props) => <Berufserfahrung {...props} />} />
        <Route exact path="/contact" render={(props) => <Kontakt {...props} />} />
      </Switch>
    </main>
  );
}
