import React from "react";
import PropTypes from "prop-types";
import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import List from "@material-ui/core/List";
import MuiListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import { withRouter } from "react-router";
import ContactMailIcon from "@material-ui/icons/ContactMail";
import AccountBoxIcon from "@material-ui/icons/AccountBox";
import BuildIcon from "@material-ui/icons/Build";
import AccountTreeIcon from "@material-ui/icons/AccountTree";
import SchoolIcon from "@material-ui/icons/School";
import { purple } from "@material-ui/core/colors";
import { setTranslations, setDefaultLanguage, setLanguageCookie, setLanguage, translate } from "react-switch-lang";
import en from "../language/en.json";
import de from "../language/de.json";
import Flag from "react-flagkit";

const drawerWidth = 240;

// Do this two lines only when setting up the application
setTranslations({ en, de });
setDefaultLanguage("de");

// If you want to remember selected language
setLanguageCookie();

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  drawer: {
    [theme.breakpoints.up("sm")]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up("sm")]: {
      width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
    color: "white",
    backgroundColor: "#232423",
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("sm")]: {
      display: "none",
    },
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
    background: "#232423",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(1),
    backgroundColor: "red",
    height: "100%",
  },
  drawerContent: {
    marginTop: "10%",
  },
  listitem: {
    [theme.breakpoints.up("sm")]: {
      paddingLeft: "12%",
      paddingRight: "5%",
      paddingTop: "20%",
      paddingBottom: "20%",
    },
  },
  languages: {
    [theme.breakpoints.up("sm")]: {
      // position: "static",
      width: 100,
    },
    display: "flex",
    // justifyContent: "center",
    // backgroundColor: "red",
  },
  flag: {
    [theme.breakpoints.up("sm")]: {
      padding: "15%",
    },
    [theme.breakpoints.down("sm")]: {
      padding: "15%",
    },
  },
  header: {
    [theme.breakpoints.up("sm")]: {
      width: "90%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "50%",
    },
  },
}));

const ListItem = withStyles({
  root: {
    "&:hover": {
      backgroundColor: purple[500],
    },
  },
})(MuiListItem);

const ResponsiveDrawer = (props) => {
  console.log(props);
  const { history } = props;
  const { window } = props;

  const { t } = props;

  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [text, setText] = React.useState(t("Drawer.first"));

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleSetLanguage = (key) => () => {
    console.log("KEY: ", key);
    setLanguage(key);
  };

  const drawer = (
    <div>
      <div className={classes.toolbar} />
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/homepage");
              setText(t("Drawer.first"));
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <AccountBoxIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={t("Drawer.first")} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/skills");
              setText(t("Drawer.second"));
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <BuildIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={t("Drawer.second")} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/myprojects");
              setText(t("Drawer.third"));
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <AccountTreeIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={t("Drawer.third")} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/journey");
              setText(t("Drawer.fourth"));
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <SchoolIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={t("Drawer.fourth")} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
      <div>
        <List>
          <ListItem
            button
            className={classes.listitem}
            onClick={() => {
              history.push("/contact");
              setText(t("Drawer.fifth"));
              if (mobileOpen) {
                handleDrawerToggle();
              }
            }}
          >
            <ListItemIcon>
              <ContactMailIcon style={{ fill: "white" }} />
            </ListItemIcon>
            <ListItemText primary={t("Drawer.fifth")} style={{ color: "white" }} />
          </ListItem>
        </List>
      </div>
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton color="inherit" aria-label="open drawer" edge="start" onClick={handleDrawerToggle} className={classes.menuButton}>
            <MenuIcon />
          </IconButton>
          <div className={classes.header}>
            <Typography variant="h6" noWrap>
              {text}
            </Typography>
          </div>

          <div className={classes.languages}>
            <div className={classes.flag}>
              <Flag country="DE" role="button" onClick={handleSetLanguage("de")} />
            </div>
            <div className={classes.flag}>
              <Flag country="GB" role="button" onClick={handleSetLanguage("en")} />
            </div>
          </div>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor={theme.direction === "rtl" ? "right" : "left"}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
    </div>
  );
};

ResponsiveDrawer.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
  t: PropTypes.func.isRequired,
};

export default withRouter(translate(ResponsiveDrawer));
// export default ResponsiveDrawer;
