import React from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import LinearProgress from "@material-ui/core/LinearProgress";
import { createMuiTheme, responsiveFontSizes, ThemeProvider } from "@material-ui/core/styles";
import Bounce from "react-reveal/Bounce";
import { Translate } from "@material-ui/icons";
import { setTranslations, setDefaultLanguage, setLanguageCookie, setLanguage, translate } from "react-switch-lang";

let theme = createMuiTheme();
theme = responsiveFontSizes(theme);

theme.typography.h4 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "1.3rem",
  },
};

theme.typography.h6 = {
  [theme.breakpoints.down("sm")]: {
    fontSize: "0.8rem",
  },
};

const BorderLinearProgress = withStyles((theme) => ({
  root: {
    height: 10,
    borderRadius: 5,
  },
  colorPrimary: {
    backgroundColor: theme.palette.grey[theme.palette.type === "light" ? 400 : 700],
  },
  bar: {
    borderRadius: 5,
    backgroundColor: "#4592b0",
  },
}))(LinearProgress);

const styles = (theme) => ({
  root: {
    // background: "linear-gradient(135deg, #bfb8d4, #232423)",
    [theme.breakpoints.down("sm")]: {
      marginLeft: 0,
      marginTop: 56,
    },
    [theme.breakpoints.up("sm")]: {
      marginLeft: 240,
      marginTop: 64,
    },
  },
  programmierenContent: {
    // marginLeft: "5%",
    paddingTop: "2%",
    [theme.breakpoints.up("sm")]: {
      width: "30%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
      paddingLeft: "10%",
      paddingRight: "10%",
      paddingBottom: "10%",
    },
  },
  anwendungenContent: {
    paddingTop: "2%",
    // marginLeft: "5%",
    [theme.breakpoints.up("sm")]: {
      width: "30%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
      paddingLeft: "10%",
      paddingRight: "10%",
      marginTop: "5%",
      paddingBottom: "10%",
    },
  },
  upperContent: {
    [theme.breakpoints.up("sm")]: {
      display: "flex",
      justifyContent: "space-evenly",
    },
    [theme.breakpoints.down("sm")]: {
      justifyContent: "center",
    },
  },
  lowerContent: {
    marginTop: "2%",
    [theme.breakpoints.up("sm")]: {
      display: "flex",
      justifyContent: "space-evenly",
      marginBottom: "2%",
    },
  },
  lowestContent: {
    marginTop: "2%",
    marginBottom: "2%",
    [theme.breakpoints.up("sm")]: {
      display: "flex",
      justifyContent: "center",
    },
  },
  frameworks: {
    paddingTop: "2%",
    [theme.breakpoints.up("sm")]: {
      width: "40%",
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
      paddingLeft: "10%",
      paddingRight: "10%",
      marginTop: "5%",
      paddingBottom: "10%",
    },
  },
});

class Skills extends React.Component {
  render() {
    const { classes } = this.props;
    const { t } = this.props;

    return (
      <div className={classes.root}>
        <div className={classes.upperContent}>
          <div className={classes.programmierenContent}>
            <ThemeProvider theme={theme}>
              <Bounce left>
                <Typography variant="h4" component="h1">
                  {t("Skills.programming")}
                </Typography>
                <Typography variant="h6" component="h1">
                  Java
                </Typography>
                <BorderLinearProgress variant="determinate" value={50} />
                <br />
                <Typography variant="h6" component="h1">
                  JavaScript
                </Typography>
                <BorderLinearProgress variant="determinate" value={70} />
                <br />
                <Typography variant="h6" component="h1">
                  C &amp; C++
                </Typography>
                <BorderLinearProgress variant="determinate" value={46} />
                <br />
                <Typography variant="h6" component="h1">
                  HTML &amp; CSS
                </Typography>
                <BorderLinearProgress variant="determinate" value={80} />
                <br />
                <Typography variant="h6" component="h1">
                  SQL
                </Typography>
                <BorderLinearProgress variant="determinate" value={60} />
                <br />
                <Typography variant="h6" component="h1">
                  DB2
                </Typography>
                <BorderLinearProgress variant="determinate" value={15} />
                <br />
                <Typography variant="h6" component="h1">
                  MongoDB
                </Typography>
                <BorderLinearProgress variant="determinate" value={85} />
                <br />
                <Typography variant="h6" component="h1">
                  Quarkus Hibernate ORM &amp; Quarkus JAX-RS
                </Typography>
                <BorderLinearProgress variant="determinate" value={45} />
              </Bounce>
            </ThemeProvider>
          </div>

          <div className={classes.anwendungenContent}>
            <Bounce right>
              <Typography variant="h4" component="h1">
                Software
              </Typography>
              <Typography variant="h6" component="h1">
                Word, Excel &amp; Powerpoint
              </Typography>
              <BorderLinearProgress variant="determinate" value={65} />
              <br />
              <Typography variant="h6" component="h1">
                LateX
              </Typography>
              <BorderLinearProgress variant="determinate" value={70} />
              <br />
              <Typography variant="h6" component="h1">
                Photoshop &amp; After Effects
              </Typography>
              <BorderLinearProgress variant="determinate" value={20} />
              <br />
              <Typography variant="h6" component="h1">
                Git
              </Typography>
              <BorderLinearProgress variant="determinate" value={60} />
              <br />
              <Typography variant="h6" component="h1">
                Virtual Box
              </Typography>
              <BorderLinearProgress variant="determinate" value={56} />
              <br />
              <Typography variant="h6" component="h1">
                Wireshark
              </Typography>
              <BorderLinearProgress variant="determinate" value={25} />
              <br />
              <Typography variant="h6" component="h1">
                Postman
              </Typography>
              <BorderLinearProgress variant="determinate" value={30} />
              <br />
              <Typography variant="h6" component="h1">
                Autodesk 3ds Max
              </Typography>
              <BorderLinearProgress variant="determinate" value={10} />
              <br />
              <Typography variant="h6" component="h1">
                Blender
              </Typography>
              <BorderLinearProgress variant="determinate" value={10} />
            </Bounce>
          </div>
        </div>
        <div className={classes.lowerContent}>
          <div className={classes.programmierenContent}>
            <Bounce left>
              <Typography variant="h4" component="h1">
                {t("Skills.operatingSystems")}
              </Typography>

              <Typography variant="h6" component="h1">
                Windows
              </Typography>
              <BorderLinearProgress variant="determinate" value={90} />
              <br />
              <Typography variant="h6" component="h1">
                Android
              </Typography>
              <BorderLinearProgress variant="determinate" value={100} />
              <br />
              <Typography variant="h6" component="h1">
                Linux
              </Typography>
              <BorderLinearProgress variant="determinate" value={30} />
              <br />
              <Typography variant="h6" component="h1">
                iOS
              </Typography>
              <BorderLinearProgress variant="determinate" value={10} />
            </Bounce>
          </div>

          <div className={classes.anwendungenContent}>
            <Bounce right>
              <Typography variant="h4" component="h1">
                IDE's
              </Typography>

              <Typography variant="h6" component="h1">
                IntelliJ
              </Typography>
              <BorderLinearProgress variant="determinate" value={90} />
              <br />
              <Typography variant="h6" component="h1">
                Eclipse
              </Typography>
              <BorderLinearProgress variant="determinate" value={40} />
              <br />
              <Typography variant="h6" component="h1">
                Visual Studio Code
              </Typography>
              <BorderLinearProgress variant="determinate" value={95} />
              <br />
              <Typography variant="h6" component="h1">
                Visual Studio
              </Typography>
              <BorderLinearProgress variant="determinate" value={71} />
            </Bounce>
          </div>
        </div>
        <div className={classes.lowerContent}>
          <div className={classes.frameworks}>
            <Bounce right>
              <Typography variant="h4" component="h1">
                Frameworks / Libraries
              </Typography>
              <Typography variant="h6" component="h1">
                React
              </Typography>
              <BorderLinearProgress variant="determinate" value={72} />
              <br />
              <Typography variant="h6" component="h1">
                Material-UI
              </Typography>
              <BorderLinearProgress variant="determinate" value={62} />
              <br />
              <Typography variant="h6" component="h1">
                Bootstrap
              </Typography>
              <BorderLinearProgress variant="determinate" value={45} />
              <br />
              <Typography variant="h6" component="h1">
                NodeJS
              </Typography>
              <BorderLinearProgress variant="determinate" value={42} />
              <br />
              <Typography variant="h6" component="h1">
                ExpressJS
              </Typography>
              <BorderLinearProgress variant="determinate" value={43} />
              <br />
              <Typography variant="h6" component="h1">
                ThreeJS
              </Typography>
              <BorderLinearProgress variant="determinate" value={51} />
              <br />
              <Typography variant="h6" component="h1">
                mongoose
              </Typography>
              <BorderLinearProgress variant="determinate" value={60} />
              <br />
              <Typography variant="h6" component="h1">
                socket.io
              </Typography>
              <BorderLinearProgress variant="determinate" value={25} />
            </Bounce>
          </div>
        </div>
      </div>
    );
  }
}

export default withStyles(styles)(translate(Skills));
